<template>
  <div class="g-container">
    <GanttConfig ref="GanttConfig" class="left-container" :tasks="tasks"></GanttConfig>
    <div>
      <button @click="setDayScale">일</button>
      <button @click="setWeekScale">주</button>
      <button @click="setMonthScale">월</button>
  </div>
  </div>
</template>

<script>
import GanttConfig from './GanttConfig.vue';

export default {
  name: 'GanttContainer',
  components: {GanttConfig},
  data () {
    return {
      tasks: {
        data: [
          {id: 1, text: '메일', member: 'aaa', start_date: '2023-10-14', end_date: '2023-10-15',duration: 1, progress: 0.6},
          {id: 2, text: '전자결재함', member: 'bbb', start_date: '2023-10-15', end_date: '2023-10-18',duration: 3, progress: 0.4}
        ],
        links: [
          {id: 1, source: 1, target: 2, type: '0'}
        ]
      },
      messages: []
    }
  },
  methods: {
    addMessage (message) {
      this.messages.unshift(message)
      if (this.messages.length > 40) {
        this.messages.pop()
      }
    },

    logTaskUpdate (id, mode, task) {
      let text = (task && task.text ? ` (${task.text})`: '')
      let message = `Task ${mode}: ${id} ${text}`
      this.addMessage(message)
    },

    logLinkUpdate (id, mode, link) {
      let message = `Link ${mode}: ${id}`
      if (link) {
        message += ` ( source: ${link.source}, target: ${link.target} )`
      }
      this.addMessage(message)
    },
    setDayScale() {
        this.$refs.GanttTest.setDayScale();
    },

    setWeekScale() {
        this.$refs.GanttTest.setWeekScale();
    },

    setMonthScale() {
        this.$refs.GanttTest.setMonthScale();
    }
  }
}
</script> 


<style>
  html, body {
    height: 100%;
    margin: 0;
    padding: 0;
  }
  .g-container {
    height: 800px;
    width: 100%;
    /* display: flex; */
  }
  .left-container {
    overflow: hidden;
    position: relative;
    height: 100%;
  }
  .right-container {
    border-right: 1px solid #cecece;
    float: right;
    height: 100%;
    width: 340px;
    box-shadow: 0 0 5px 2px #aaa;
    position: relative;
    z-index:2;
  }
  .gantt-messages {
    list-style-type: none;
    height: 50%;
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding-left: 5px;
  }
  .gantt-messages > .gantt-message {
    background-color: #f4f4f4;
    box-shadow:inset 5px 0 #d69000;
    font-family: Geneva, Arial, Helvetica, sans-serif;
    font-size: 14px;
    margin: 5px 0;
    padding: 8px 0 8px 10px;
  }

  .gantt_grid {
    overflow-y: hidden !important;
}

.gantt_task {
    overflow-y: auto !important;
}
</style>