<template>
  <div v-if="visible" class="app-t-modal">
    <div class="app-modal-content">
      <div class="app-modal-header">Search Member</div>
      <div class="app-modal-body">
        <div class="a-m-search">
          <input class="s-selForm" v-model="searchValue" @keyup.enter="searchMember" />
          <button class="m-i-search" @click="searchMember">Search</button>
        </div>
        <div class="a-m-list">
          <table class="m-s-list-table">
            <tr v-for="(member, index) in members" :key="index" @click="selectMember(member)">
              <td>{{ member.div_name }}</td>
              <td>{{ member.member_name }}</td>
              <td>{{ member.rank }}</td>
            </tr>
          </table>
        </div>
        <div class="s-list-div">
          <div v-html="selectedMembers.join('<br>')"></div>
        </div>
      </div>
      <div class="app-modal-footer">
        <button class="m-confirm-btn" @click="searchAllMember">Confirm</button>
        <button class="m-cancel-btn" @click="closeModal">Cancel</button>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name:"MemberSearchModal",
  props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
  data() {
    return {
      searchValue: "",
      members: [],
      selectedMembers: [],
      key:'a13da97b-0b47-48ee-b8e1-62c1c5464817',
    };
  },
  mounted() {
    this.checkInput();
  },
  methods: {
    checkInput(){
      console.log('checkInput');
      if(document.getElementsByName('tKey').length > 0  && document.getElementsByName('tKey')[0].value){
        console.log(document.getElementsByName('tKey')[0].value);
        this.getTodo(document.getElementsByName('tKey')[0].value);
      }else{
        console.log('not found input');
      }
      
    },
    getTodo(value){
      this.key = value;
      this.fetchData();
    },
    forTest(temp){
      console.log("key : " + temp);
      this.key = temp;
      this.searchAllMember();
    },
    async searchAllMember(){
        try {
        const response = await this.$apiClient.get("/timeline?t_key="+this.key);
        this.members = response.data;
        console.log("성공>>>"+response);
      } catch (error) {
        console.log(error);
      }

    },

    // searchMember() {
    //   this.$apiClient
    //     .get("searchMember", {
    //       params: {
    //         searchCondition: this.searchCondition,
    //         searchValue: this.searchValue,
    //       },
    //     })
    //     .then((response) => {
    //       this.members = response.data;
    //     })
    //     .catch(() => {
    //       alert("사원 목록 검색 실패");
    //     });
    // },
    selectMember(member) {
      const exists = this.selectedMembers.find(
        (m) => m.member_num === member.member_num
      );
      if (exists) {
        this.selectedMembers = this.selectedMembers.filter(
          (m) => m.member_num !== member.member_num
        );
      } else {
        this.selectedMembers.push(member);
      }
    },
    closeModal() {
      this.$emit('close');
    },
  } 
};
</script>






<style scoped>
.app-t-modal {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-modal-content {
  width: 500px;
  background: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.app-modal-header,
.app-modal-footer {
  margin-bottom: 20px;
}

.a-m-search {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.s-selForm {
  flex-grow: 1;
  margin-right: 10px;
}

.m-i-search {
  padding: 8px 16px;
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.a-m-list {
  overflow-y: auto;
  height: 150px;
  border: 1px solid #ccc;
  margin-bottom: 15px;
}

.m-s-list-table {
  width: 100%;
  border-collapse: collapse;
}

.m-s-list-table tr:hover {
  background-color: rgba(117, 144, 194, 0.25);
}

.s-list-div {
  margin-top: 15px;
  border: 1px solid #ccc;
  padding: 10px;
  overflow-y: auto;
  height: 90px;
}

.m-confirm-btn,
.m-cancel-btn {
  padding: 8px 16px;
  margin: 5px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.m-confirm-btn {
  background-color: #007BFF;
  color: #fff;
}

.m-cancel-btn {
  background-color: #ccc;
}


</style>