<template>
  <div ref="gantt"></div>
</template>

<script>
import {gantt} from 'dhtmlx-gantt';
export default {
  name: 'GanttConfig',
  props: {
    tasks: {
      type: Object,
      default () {
        return {data: [], links: []}
      }
    }
  },
methods:{
  //월/주/일 단위로 보기
  setDayScale() {
        gantt.config.scale_unit = "day";
        gantt.config.date_scale = "%d %M";
        gantt.config.subscales = [];
        gantt.render();
    },

    setWeekScale() {
    gantt.config.scale_unit = "week";
    gantt.templates.date_scale = function(date) {
        let weekNum = gantt.date.date_to_str("%W")(date);
        return `Week ${weekNum}`;
    };
    gantt.config.subscales = [
        {unit: "day", step: 1, date: "%d %M"}
    ];
    //gantt.config.scale_cell_width = 30;
    gantt.render();
},

    setMonthScale() {
        gantt.config.scale_unit = "month";
        gantt.config.date_scale = "%F %Y";
        gantt.config.subscales = [
            {unit: "week", step: 1, date: "Week %W"}
        ];
        gantt.render();
    }
},

  mounted: function () {
    gantt.config.xml_date = "%Y-%m-%d";
    gantt.init(this.$refs.gantt);
    
    gantt.config.columns=[
      {name:"text", label:"Task name", tree:true, width:130 },
      {name:"member", label:"Member name",align: "center"},
      {name:"start_date", label:"Start time", align: "center" },
      // {name:"duration",   label:"Duration",   align: "center" },
      {name:"end_date", label:"End time", align: "center" },
      {name:"add",        label:"" }
    ];
    gantt.config.grid_width = 450; 
    //주 단위로 보기
    //gantt.config.scale_unit = "week";
    gantt.config.scrollable = true;
    gantt.config.scale_unit = "day";

    gantt.config.show_links = false;

   //todo 추가 모달창
   gantt.config.lightbox.sections = [
   {name: "To Do", height: 70, map_to: "text", type: "textarea"},
   {name: "customTime", height: 72, type: "time", map_to: "auto"}
];

    gantt.createDataProcessor((entity, action, data, id) => {
  this.$emit(`${entity}-updated`, id, action, data);
});
    gantt.parse(this.$props.tasks);
    
  },
  
}
</script>

<style>
    @import "~dhtmlx-gantt/codebase/dhtmlxgantt.css";
</style>